/* Badge Component - Following Scale Pattern with Dive Tokens */

:host {
  /* Default badge styling using dive design tokens */
  --badge-background: var(--Color-Base-Subtle-Background-default);
  --badge-color: var(--Color-Base-Subtle-Foreground-default);
  --badge-padding: var(--Spacing-1) var(--Spacing-4);
  --badge-border-radius: var(--border-border-radius-full);
  --badge-border: var(--border-border-width-default) solid transparent;
  --badge-font-size: 12px;
  --badge-font-weight: 500;
  --badge-line-height: 1.33;
}

.badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: var(--badge-padding);
  border-radius: var(--badge-border-radius);
  border: var(--badge-border);
  font-size: var(--badge-font-size);
  font-weight: var(--badge-font-weight);
  line-height: var(--badge-line-height);
  white-space: nowrap;
  cursor: default;
  box-sizing: border-box;
  user-select: none;
  background-color: var(--badge-background);
  color: var(--badge-color);
}

/* Badge Type Variants */
.badge--type-primary {
  --badge-background: var(--Color-Primary-Primary-Background-default);
  --badge-color: var(--Color-Primary-Primary-Foreground-default);
}

.badge--type-success {
  --badge-background: var(--Color-Success-Primary-Background-default);
  --badge-color: var(--Color-Success-Primary-Foreground-default);
}

.badge--type-warning {
  --badge-background: var(--Color-Warning-Primary-Background-default);
  --badge-color: var(--Color-Warning-Primary-Foreground-default);
}

.badge--type-error {
  --badge-background: var(--Color-Error-Primary-Background-default);
  --badge-color: var(--Color-Error-Primary-Foreground-default);
}

.badge--type-info {
  --badge-background: var(--Color-Info-Primary-Background-default);
  --badge-color: var(--Color-Info-Primary-Foreground-default);
} 